import {truncate} from 'fs';
import Modal from "../component/modal";
import {getCardInfo, getCardList, getOpenid, unLinkCard, updateCardName} from "../../api/card";
import Page from "../component/page/page";
import Status from "../component/status";

const utils = require("../../utils/utils")

export default {
    components: {Status, Page, Modal},
    data() {
        return {
            modalEditName: false,
            modalUnBindCard:false,
            buttons:[{
                text:'添加卡片',
                key:'add_card'
            }],
            cardCur:0,
            StatusBar: this.StatusBar,
            CustomBar: this.CustomBar,
            colorMap : { '正常':'bg-green','已挂失':'bg-orange','冻结':'bg-red'},
            cardBg:[
                "https://deepand.oss-cn-shenzhen.aliyuncs.com/icon/bg1.jpg",
                "https://deepand.oss-cn-shenzhen.aliyuncs.com/icon/visa-bg-3.jpg",
                "https://deepand.oss-cn-shenzhen.aliyuncs.com/icon/visa-bg-4.jpg"
            ],
            dotStyle: false,
            towerStart: 0,
            direction: '',
            cardInfo:{},
            cards:[],
            title: "Hello",
            index: 0,
            elements: [
                {
                    key: 'card_edit',
                    title: "卡操作",
                    name: "限购设置 卡挂失",
                    color: "cyan",
                    icon: "newsfill",
                    url:'../editCard/editCard'
                },
                {
                    key: 'recharge',
                    title: "账户充值",
                    name: "4月16日 充值100",
                    color: "blue",
                    icon: "pay",
                    url:"../recharge/recharge"
                },
                {
                    title: "消费记录",
                    name: "consume",
                    color: "purple",
                    icon: "refund",//consumeRecord
                    url:'../consumeRecord/consumeRecord'
                },
                {
                    title: "充值记录 ",
                    name: "recharge",
                    color: "mauve",
                    icon: "moneybagfill",
                    url:"../rechargeRecord/rechargeRecord"
                }
            ]
        };
    },
    async onLoad() {
       this.loadData();

        // this.elements[1]['name']=cards[0]['recharge'];

    },
    async onShow(){
        this.loadData();
        // return 0;
        // this.cardInfo = await  getCardInfo({cardno:this.cardInfo['cardno']});
        //
        // this.elements[1]['name'] = this.cardInfo['lastrecharge']['date'];
    },

    methods: {
        handleButtonTap({key}){
            // console.log(`调试:`, e)
            if(key === 'add_card'){
                this.handleAddCard();
            }

        },
        async loadData(){
            let cards =await getCardList();
            if(cards.length <1 ){
                this.cards = [];
                return  0
            }
            this.cards = cards;
            this.switchCard(0);
        },
        // 卡片菜单点击
        handleElementsTap(e){
            let  { currentTarget } = e,{ dataset } = currentTarget, {data }= dataset;

            switch (data.key) {
                case "card_edit":
                    utils.navigateTo({
                        url:data.url,
                        params:{
                            ...this.cardInfo
                        }
                    });
                break;
                case "recharge":
                    utils.navigateTo({
                        url:data.url,
                        params:{
                            ...this.cardInfo
                        }
                    });

                 break;
                default:
                    utils.navigateTo({
                        url:data.url,
                        params:{
                            ...this.cardInfo
                        }
                    });
                    break;

            }

            console.log(`调试:data`, data)
        },
        async cardSwiper(e) {
            this.cardCur = e.detail.current;
            this.switchCard(this.cardCur);
        },
        async switchCard(index){
            this.cardInfo = await  getCardInfo({cardno:this.cards[index]});
            console.log(`调试:switchCard方法中 当前cards`, this.cards);
            console.log(`调试: 当前cardinfo`, this.cardInfo);
            // let {date,money,give} = this.cardInfo['lastrecharge'];
            this.elements[1]['name'] =  this.cardInfo['lastrecharge'];
        },
        handleNavigatorClick(e){
            let {target:{dataset:{url}}} = e;
            uni.navigateTo({
                url
            })
            console.log(`调试:`, url)
        },
        PickerChange(e) {
            this.cardCur = e.detail.value;
        },
        handleEditName() {
            console.log(`调试:事件调用成功`);
            this.modalEditName = true;
        },
        handleNameChange(e){
           const {detail:{value}} = e;
           this.tempCardName = value;
           console.log(`调试:`, value)
        },
        //更改名字提交
        async handleEditNameCancel(){
            // this.cards[this.cardCur]['name']=this.tempCardName;
            // uni.showLoading({ title:'请稍后...' });
            // let  cardno = this.cardInfo.cardno;
            // let name = this.cardInfo.cardname
            // await  updateCardName({cardno,name});
            // uni.hideLoading();
            uni.showToast({
                title:'取消了操作'

            });

            console.log(`调试:你点击了确定`)
        },    //更改名字提交
        async handleEditSubmit(){
            // this.cards[this.cardCur]['name']=this.tempCardName;
            uni.showLoading({ title:'请稍后...' });
            let  cardno = this.cardInfo.cardno;
            let name = this.cardInfo.cardname
            await  updateCardName({cardno,name});
            uni.hideLoading();
            uni.showToast({
                type:'success',
                title:'修改成功'
            });

            console.log(`调试:你点击了确定`)
        },
        //显示解绑卡片提示
        showUnbindModal(){
            this.modalUnBindCard = true
        },
        //解绑卡片提交
       async handleUnBindCard(){
            let  com = await  utils.comfirm({
                title:'解绑卡片',
                content:'是否确认解绑'
            });
            if(com){
                uni.showLoading({ title:'解绑中...' });
                let res =await unLinkCard({cardno:this.cardInfo.cardno}).catch(err=>{
                    uni.hideLoading()
                    uni.showToast({
                        icon:'none',
                        title:'解绑失败'
                    })
                });
                console.log(`调试:解绑返回值`, res);
                uni.hideLoading()

                uni.showToast({
                    title:'解绑成功'
                })
                // this.loadData();
                uni.reLaunch({
                    url:'../index/index'
                })

            }

        },
        //添加卡片
        handleAddCard(){
            uni.navigateTo({
                url:'../addCard/addCard'
            })
        }

    }
};
